<template>
  <div class="text-left">
    <div v-show="display" class="container-card">
      <!-- 搜索栏 -->
      <el-row class="serach-box">
        <el-col :span="4">
          <el-col :span="7">样品号</el-col>
          <el-col :span="16"><el-input v-model="userQuery.getAllDetail.sampleNubmber" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">表单名称</el-col>
          <el-col :span="16"><el-input
            v-model="userQuery.alias"
            clearable
            placeholder="请输入内容"
          >
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">业务员</el-col>
          <el-col :span="16"><el-input v-model="userQuery.getAllDetail.salesMan" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">客户</el-col>
          <el-col :span="16"><el-input v-model="userQuery.getAllDetail.customer" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">针形</el-col>
          <el-col :span="16"><el-input v-model="userQuery.getAllDetail.needleCount" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input></el-col>
        </el-col>
         <el-col :span="4">
          <el-date-picker
            v-model="dataValue"
            type="daterange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-col>
      </el-row>
      <!-- 按钮区 -->
      <el-row class="serach-box">
         <el-col :span="4">
          <el-col :span="7">状态</el-col>
          <el-col :span="16"><el-select
            class="status-select"
            v-model="userQuery.samplePlanStateBatch"
            placeholder="请选择模板"
          >
            <el-option
              v-for="item in statusTypeList"
              :key="item.index"
              :value="item"
            >
            </el-option>
          </el-select></el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">批量修改</el-col>
          <el-col :span="16"><el-select v-model="value" placeholder="请选择模板">
            <el-option
              v-for="item in statusChangeList"
              :key="item.index"
              :value="item"
            >
            </el-option>
          </el-select></el-col>
        </el-col>
        <el-col class="btn-area" :span="8">
          <el-col :span="6"><el-button type="info">更新状态</el-button></el-col>
          <el-col :span="6"><el-button type="info" @click="newGroup">新增</el-button></el-col>
          <el-col :span="6"><el-button type="danger" @click="remove"
            >删除</el-button
          ></el-col>
        </el-col>
      </el-row>
      <!-- 日期筛选区 -->
      <el-row class="serach-box">
          <el-col class="status-area" :span="8">
          <span>已完成:{{finished}}</span>
          <span>未完成:{{notFinished}}</span>
          <span>修改中:{{modified}}</span>
          <span>取消:{{cancel}}</span>
        </el-col>
       
      </el-row>
      <!-- 表单区 -->
      <el-table @select="rowclick" :data="tableData" border style="width: 100%">
        <el-table-column type="selection" fixed width="55"> </el-table-column>
        <el-table-column type="index"  label="序号" fixed width="80"></el-table-column>
        <el-table-column
          :fixed="item.isFix || false"
          :label="item.label"
          :prop="item.prop"
          :width="item.width"
          v-for="item in tableList"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column label="操作" width="80">
          <template slot-scope="scope">
            <el-button @click="edit(scope)" type="text" size="small"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <new-proof-group v-show="!display" :object2="object2"></new-proof-group> -->
  </div>
</template>

<script>
import {
  GetAllSamplePlansBatchOverview,
  DeleteBatchSamplePlans,
} from "network/proofGroup.js";
import { GetCommonFormState } from "network/common.js";
/* import NewProofGroup from "views/Home/Business/NewProofGroup.vue"; */

export default {
  data() {
    return {
      tableData: [
      ],
      display: true,
      totalAmount: null,
      finished: null,
      notFinished: null,
      modified: null,
      cancel: null,
      object2: [],
      dataValue: null,
      selectData: [],
      statusTypeList: [],
      statusChangeList: [],
      tableList: [
        { label: "表单名称", prop: "alias", width: "100", isFix: "left" },
        { label: "样品号", prop: "sampleNubmber", width: "100", isFix: "left" },
        { label: "业务员", prop: "salesMan" },
        { label: "客户", prop: "customer" },
        { label: "品名", prop: "machineType" },
        { label: "创建日期", prop: "creationTime" },
        { label: "状态", prop: "samplePlanStateBatch" },
      ],
      headerItems:[
        {
        label:"样品号",
        prop:"sampleNubmber",
      },{
        label:"表单名称",
        prop:"alias",
      },{
        label:"业务员",
        prop:"salesMan",
      },{
        label:"客户",
        prop:"customer",
      },{
        label:"针型",
        prop:"needleCount",
      }
      ],
      userQuery: {
        alias:"",
        samplePlanStateBatch:"完成",
        beginTime:"2021-01-01",
        endTime:"2021-02-25",
        pageSize:"6",
        pageNumber:"0",
        getAllDetail:{
          salesMan:"",
          customer:"",
          needleCount:"",
          sampleNubmber:""
        }
      },
      value:""
    };
  },
  created() {
    this.inital();
  },
  watch: {
    dataValue(val) {
      this.userQuery.beginTime = val[0];
      this.userQuery.endTime = val[1];
      this.query();
    },
  },
  methods: {
    //初始化
    inital() {
      this.getProofGroup(this.userQuery);
      this.getStatusList();
    },
    //获得表状态列表
    getStatusList() {
      GetCommonFormState().then((res) => {
        if (res.success) {
          this.statusTypeList = res.result;
          this.statusChangeList = res.result;
        }
      });
    },
    //获得打样单单个列表
    getProofGroup(data) {
      GetAllSamplePlansBatchOverview(data).then((res) => {
        if (res.success) {
          this.tableData = res.result.samplePlansOverview.items;
          this.totalAmount = res.result.totalAmount;
          this.finished = res.result.finished;
          this.notFinished = res.result.notFinished;
          this.modified = res.result.modified;
          this.cancel = res.result.cancel;
          this.$message.success("获得订打样单列表成功");
        } else {
          this.$message.error("获得打样单列表失败");
        }
      });
    },
    //点击选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //点击查询
    query(){
      this.getProofGroup(this.userQuery);
    },
    //新增
    newGroup() {
      this.$router.push("/newProofGroup")
    },
    //编辑
    edit(scope) {
      this.$router.push({
        path:"/newProofGroup",
        query:{
          iid:scope.row.id
        }
      })
    },
    //批量删除
    remove(){
      var data = this.selectData;
      if(data.length==0){
        this.$message.error('请先选择项！');
      }
      else{
        let arr = new Array(data.length);
        data.forEach((item,index) => {
          arr[index] = item.id;
        });
        DeleteBatchSamplePlans(arr).then(res=>{
          if(res.success){
            this.$message.success("删除成功")
          }
        })
      }
    },
    //删除操作
    delete(){
      DeleteBatchSamplePlans(data).then(res=>{
        console.log(res);
      })
    }
  },
};
</script>

<style scoped>
.btn-area button {
  width: 120px;
}
.el-row{
  margin-bottom: 10px;
}
.status-area span {
  line-height: 40px;
  margin-right: 10px;
}
.btn-area .el-button{
  width:100px;
}
</style>