import {request} from './request'

//获得打样单列表
export async function GetAllSamplePlansBatchOverview(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllSamplePlansBatchOverview',
        data:data
    })
}

//新增打样单
export async function AddBatchSamplePlans(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/AddBatchSamplePlans',
        data:data
    })
}

//更新打样单
export async function UpdateBatchSamplePlans(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateBatchSamplePlans',
        data:data
    })
}

//删除打样单
export async function DeleteBatchSamplePlans(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBatchSamplePlans',
        params:{
            input:data
        }
    })
}

//获取单个打样单
export async function GetSingleSamplePlanBatch(data){
    return await request({
        url:'/api/BusinessUnit/GetSingleSamplePlanBatch/Single',
        params:{input:data}
    })
}